import React, {Component} from 'react'
import CountUp from 'react-countup';


export default class Globaldig extends Component {

    render(props) {
        const {confirmed, recovered, deaths,lastUpdate} = this.props.dig;
        const {chk} = this.props.inf
        if (this.props) {
            console.log(`data is ${this.props.inf}`);
        }

        if (recovered) {
            // console.log(this.props.dig.recovered); console.log(recovered.value);
            // console.log(confirmed.value); console.log(deaths.value);

        }
        return (
            <div>
                {recovered
                    ? <div className="">
                            <div
                        className="row mt-5 justify-content-md-center align-items-center container-fluid">

                        <div className="col-12 col-lg-4 col-md-4 col-sm-12 my-5 text-center">
                            <div
                                className="card card-sm shadow-lg p-3 mb-5 bg-white rounded "
                                style={{
                                borderBottom: "7px solid red"
                            }}>
                                <div className="card-body">
                                    <h3 className="card-title">
                                        Confirmed
                                    </h3>
                                    <h6 className="text-muted">Country : {this.props.inf}</h6>
                                    <hr/>
                                    <hr/>
                                    <h5 className="card-subtitle mb-2 text-muted text-capitalize">number of active cases of covid -19</h5>
                                    <h2 className="card-text">{< CountUp start = {
                                            0
                                        }
                                        end = {
                                            confirmed.value
                                        }
                                        duration = {
                                            1.5
                                        }
                                        separator = ',' />}</h2>
                                        <p className="text-capitalize">last updated on : {lastUpdate}</p>

                                </div>
                            </div>
                        </div>

                        <div className="col-12 col-lg-4 col-md-4 col-sm-12 my-5 text-center">
                            <div
                                className="card deaths shadow-lg p-3 mb-5 bg-white rounded"
                                style={{
                                borderBottom: "7px solid grey"
                            }}>
                                <div className="card-body">
                                    <h3 className="card-title">Deaths
                                    </h3>
                                    <h6 className="text-muted">Country : {this.props.inf}</h6>
                                    <hr/>
                                    <hr/>
                                    <h5 className="card-subtitle my-3 text-muted text-capitalize">number of persons dead of covid -19</h5>

                                    <h2 className="card-text">{< CountUp start = {
                                            0
                                        }
                                        end = {
                                            deaths.value
                                        }
                                        duration = {
                                            1.5
                                        }
                                        separator = ',' />}</h2>
                                        <p className="text-capitalize">last updated on : {lastUpdate}</p>

                                </div>
                            </div>
                        </div>

                        <div className="col-12 col-lg-4 col-md-4 col-sm-12 my-5 text-center">
                            <div
                                className="card recovered shadow-lg p-3 mb-5 bg-white rounded "
                                style={{
                                borderBottom: "7px solid green"
                            }}>
                                <div className="card-body">
                                    <h3 className="card-title">Recovered
                                    </h3>
                                    <h6 className="text-muted">Country : {this.props.inf}</h6>
                                    <hr/>
                                    <hr/>
                                    <h5 className="card-subtitle mb-2 text-muted text-capitalize">number of persons Recovered of covid -19</h5>
                                    <h2 className="card-text">{< CountUp start = {
                                            0
                                        }
                                        end = {
                                            recovered.value
                                        }
                                        duration = {
                                            1.5
                                        }
                                        separator = ',' />}</h2>
                                        <p className="text-capitalize mb=0 pb-0">last updated on : {lastUpdate}</p>

                                </div>
                            </div>
                        </div>
                    </div>
                        </div>
                    : null}

            </div>
        )
    }
}

// export {getdata};